* {
  margin: 0;
  user-select: none;
}
.wrapper {
  margin-bottom: 20px;
}
.h3 {
  color: #222;
  font-size: 22px;
  font-weight: normal;
  text-align: left;
  padding: 0 10vw;
  font-family: "Poiret One";
  text-transform: uppercase;
  line-height: 1.3;
  position: relative;
}
.span {
  color: rgba(105, 201, 202, .4);
  font-size: 48px;
  display: inline-block;
  position: absolute;
  top: -27px;
  left: 7vw;
  font-family: "Open Sans";
  font-weight: bold;
}
@media(min-width: 1200px) {
  .wrapper {
    margin-bottom: 28px;
  }
  .h3 {
    text-align: left;
    font-size: 48px;
    margin-left: 100px;
    position: unset;
    padding: 0;
    transform: translate(0, 0);
    line-height: 1;
  }
  .span {
    font-size: 100px;
    transform: translate(80px, 0);
    position: unset;
  }
}
