.wrapper {
  display: flex;
  justify-content: center;
}

button {
  background: none;
  font-size: 14px;
  font-weight: 300;
  width: 155px;
  padding: 15px 0;
  text-align: center;
  line-height: 1;
  cursor: pointer;
  font-family: "Open Sans";
}
button:hover, button:active, button:focus {
  background: none;
  outline: none;
}

.order {
  border: 1px solid #69c9ca;
  color: #69c9ca;
  margin-right: 4vw;
}
.more {
  border: 1px solid #575757;
  color: #575757;
  text-decoration: none;
  background: none;
  font-size: 14px;
  font-weight: 300;
  width: 155px;
  padding: 15px 0;
  text-align: center;
  line-height: 1;
  cursor: pointer;
  font-family: "Open Sans";
}

@media(min-width: 1200px) {
  .wrapper {
    justify-content: space-between;
  }
  button {
    width: 15vw;
    padding: 18px 0;
    font-size: 18px;
    transition: all .4s ease;
  }
  .order:hover {
    color: #ffffff;
    border: 2px solid #69c9ca;
    background: #69c9ca;
  }
  .more:hover {
    color: #ffffff;
    border: 2px solid #575757;
    background: #575757;
  }
  .order {
    margin-right: 0;
    border: 2px solid #69c9ca;
  }
  .more {
    border: 2px solid #575757;
    width: 15vw;
    padding: 18px 0;
    font-size: 18px;
    transition: all .4s ease;
  }
}
