@media(min-width: 1200px) {
  .wrapper {
    width: 30px;
  }
  .wrapperBlack {
    border-right: 1px solid #000;
  }
  .wrapperWhite {
    border-right: 1px solid #fff;
  }
  .list {
    padding: 0;
    margin: 0;
    list-style: none;
    padding: 15px 0;
    z-index: 1;
  }
  .listItem {
    height: 31px;
    border-right: 5px solid transparent;
    transition: all 0.4s ease;
    margin: 0;
    padding: 0;
    z-index: 2;
    transform: translate(3px, 0);
  }
  .listItem:hover {
    border-right: 5px solid #69c9ca;
  }
  .listItem:not(:last-child) {
    margin-bottom: 5px;
  }
  .active {
    border-right: 5px solid #69c9ca;
  }
  .link {
    font-size: 13px;
    display: block;
    margin: 0;
    padding: 0;
    text-decoration: none;
    line-height: 31px;
    cursor: pointer;
  }
  .linkBlack {
    color: #000;
  }
  .linkBlack:hover {
    color: #000;
  }
  .linkWhite {
    color: #fff;
  }
  .linkWhite:hover {
    color: #fff;
  }
  .link:hover {
    text-decoration: none;
  }
}
