.section {
  height: 690px;
  background: #fff;
  background-size: cover;
  clip-path: polygon(0% 0%, 100% 5%, 100% 100%, 0% 95%);
  transform: translate(0, -10%);
  -ms-transform: translate(0, 0);
  padding-top: 110px;
  background: url('../../img/background/fourthScreenSomethingMob.png');
  background-image: -webkit-image-set(url('../../img/background/fourthScreenSomethingMob.webp') 1x, url('../../img/background/fourthScreenSomethingMob.png'));
  background-repeat: no-repeat;
  background-position: 100% 10%;
}

@media(min-width: 1200px) {
  .section {
    height: 886px;
    clip-path: polygon(0% 0%, 100% 10%, 100% 100%, 0% 90%);
    transform: translate(0, -20%);
    -ms-transform: translate(0, 0);
    padding-top: 89px;
    position: relative;
    background: url('../../img/background/fourthScreenSomethingDesk.png');
    background-image: -webkit-image-set(url('../../img/background/fourthScreenSomethingDesk.webp') 1x, url('../../img/background/fourthScreenSomethingMob.png'));
    background-repeat: no-repeat;
    background-position: 100% 10%;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30%;
  }
  .img {
    display: block;
    position: absolute;
    height: 56%;
    width: auto;
    left: 55%;
    bottom: 19%;
    z-index: -1;
  }
}
