.wrapper {
  width: 100%;
}
.text {
  font-size: 14px;
  font-weight: 300;
  margin-bottom: 20px;
  padding: 0 7vw;
  color: #222;
}
.img {
  display: block;
  margin: 0 auto;
  height: 220px;
  width: auto;
  margin-bottom: 30px;
}
.buttons {
  padding: 0 7vw;
}

@media(min-width: 1200px) {
  .wrapper {
    width: 35vw;
    padding: 0;
    margin-left: 218px;
  }
  .text {
    padding: 0;
    margin-bottom: 62px;
    line-height: 1.67;
  }
}
